h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.header {
  background-color: #2c2c2c;
  border-radius: 0 0 30px 30px;
  overflow: hidden;
}

.header-container {
  flex-direction: column;
  align-items: center;
  gap: 48px;
  display: flex;
}

@media (width >= 640px) {
  .header-container {
    gap: 56px;
  }
}

@media (width >= 1024px) {
  .header-container {
    gap: 48px;
  }
}

.header__navbar {
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 40px);
  margin: 20px 20px 0;
  display: flex;
  position: relative;
}

@media (width >= 640px) {
  .header__navbar {
    width: calc(100% - 82px);
    margin: 20px 41px 0;
  }
}

@media (width >= 1024px) {
  .header__navbar {
    width: 880px;
    margin: 48px 54px 0;
  }
}

@media (width >= 1600px) {
  .header__navbar {
    width: 1062px;
  }
}

.header__navbar-icons {
  justify-content: space-between;
  align-items: flex-end;
  width: 102px;
  display: flex;
  position: relative;
}

@media (width >= 1024px) {
  .header__navbar-logo {
    transition: transform .4s;
  }

  .header__navbar-logo:hover {
    transform: scale(1.1);
  }
}

.header__navbar-list {
  opacity: 0;
  background-color: #fff;
  border-radius: 8px;
  width: 150px;
  padding: 10px 0;
  list-style: none;
  transition: opacity .4s, transform .4s;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  transform: translateY(-10px);
}

.header__navbar-list.active {
  opacity: 1;
  display: block;
  transform: translateY(0);
}

@media (width >= 1024px) {
  .header__navbar-list.active {
    opacity: 1;
    background-color: #0000;
    flex-direction: row;
    justify-content: space-between;
    gap: 48px;
    margin: 0;
    padding: 0;
    display: flex;
    position: static;
    transform: none;
  }
}

.header__navbar-desktop-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}

@media (width >= 1024px) {
  .header__navbar-desktop-list {
    gap: 48px;
    display: flex;
  }
}

.header__navbar-item {
  margin: 0;
