    padding: 0;
  }
}

.header__navbar-link {
  color: #333;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  text-decoration: none;
  display: block;
}

@media (width >= 1024px) {
  .header__navbar-link {
    color: #fff;
    letter-spacing: 3px;
    text-transform: uppercase;
    justify-content: center;
    align-items: center;
    margin: 0;
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-weight: 700;
    line-height: 150%;
    display: flex;
  }

  .header__navbar-link:hover {
    color: #2060f6;
  }
}

.header__navbar-button {
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #fff;
  height: 30px;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 150%;
  text-decoration: none;
  position: relative;
}

.header__navbar-button:after {
  content: "";
  transform-origin: 100%;
  background-color: #2060f6;
  border-radius: 4px;
  width: 100%;
  height: 2px;
  transition: transform .4s;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scale(1);
}

@media (width >= 1024px) {
  .header__navbar-button:hover:after {
    transform: scale(0);
  }
}

.header__navbar-menu {
  cursor: pointer;
}

@media (width >= 1024px) {
